.detail {
  margin: 30px auto 0;
  width: 90%;
  max-width: 400px;
  background-color: white;
  text-align: center;
  padding: 50px 10px;
  box-sizing: border-box;
  box-shadow: 1px 2px 10px 2px gray;
}

.detail_genres {
  display: flex;
  justify-content: center;
  gap: 0 5px;
  color: gray;
  margin-bottom: 30px;
}

.detail_summary {
  word-break: keep-all;
}

.push {
  width: 100%;
  height: 50px;
}